p,
h1,
h2,
h3,
h4,
h5,
h6,
em,
div,
li,
span,
strong {
  color: #e8e8e8;
  font-weight: 300;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.App {
  background-color: #1c1c1d;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  position: sticky;
  top: 0;
  background-color: #1c1c1d;
}

a.anchor {
  display: block;
  position: relative;
  top: -9vh;
  visibility: hidden;
}

/* For Firefox */
html {
  scrollbar-width: thin; /* 'auto' or 'thin' */
  scrollbar-color: #888 #f5f5f5; /* thumb and track color */
}

/* For Google Chrome, Safari, and newer versions of Opera and Edge */
::-webkit-scrollbar {
  width: 12px; /* scrollbar width */
  background-color: none; /* scrollbar background color */
}

::-webkit-scrollbar-thumb {
  background-color: #888; /* scrollbar thumb color */
  border-radius: 10px;
  border: 3px solid black; /* Should match the background-color of the scrollbar */
}

::-webkit-scrollbar-thumb:hover {
  background: #555; /* scrollbar thumb color on hover */
}

::-webkit-scrollbar-track {
  background-color: #1c1c1d; /* Or any other color you prefer */
}
